import React from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import '../../units.css';
import Content from '../../../../ContentJSON/U1/4Funciones.json';
import YoutubeLink from "../../../YouTubeLink";

const Funciones = () => {
    const StyledTableCell = styled(TableCell)(({ theme }) => ({
        [`&.${tableCellClasses.head}`]: {
          backgroundColor: '#4051B5',
          color: theme.palette.common.white,
        },
    }));

    return (
        <div id='u1-funciones'>
            <div className='unit-subtitle'>
                <label>{Content["p0"]}</label>
            </div>
            <div className='unit-content'>
                <p>{Content["p1"]}</p>
                <div className='unit-image2'>
                    <img src={"/images/U1T4F1.jpg"} className='unit-image' alt="Sintaxis Funcin"/>
                </div>
                <p>{Content["p2"]}</p>
                <p>{Content["p3"]}</p>
                <div className='unit-image3'>
                    <img src={"/images/U1T4F2.jpg"} className='unit-image' alt="Sintaxis Funcin"/>
                </div>
                <YoutubeLink embedId="T6u7s_5FtmY"/>
                <p></p>
                <YoutubeLink embedId="GBCU2uaACII"/>
                <p>{Content["p4"]}</p>
                <p><b>{Content["p5"]}</b></p>
                <p>{Content["p6"]}</p>
                <div className='unit-table'>
                    <TableContainer component={Paper} elevation={4}>
                        <Table size="small" aria-label="simple table">
                            <TableHead>
                            <TableRow>
                                <StyledTableCell>Funcin</StyledTableCell>
                                <StyledTableCell>Descripcin</StyledTableCell>
                            </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {"=ALEATORIO.ENTRE(nmero1;nmero2)"}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve un nmero aleatorio entre los valores nmero1 y nmero2 entregados'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=REDONDEAR(nmero;cantidadDecimales)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve el nmero entregado redondeado a la cantidad de decimales especificados'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=SUMA(nmero1;nmero2;...)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve la suma de todos los nmeros entregados. Recibe una cantidad variable de argumentos.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=SUMAR.SI(rangoNmeros;criterio;[rangoSuma])'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'A partir del rango de nmeros entregado, verifica elemeno por elemento si cumple el criterio lgico proporcionado, en caso de cumplirlo suma los elemento correspondiente de rangoSuma, si ste no fue proporcionado realiza la suma con rangoNmeros. Para una explicacin ms detallada revise los videos adjuntos.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=PROMEDIO(nmero1;nmero2;...)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve el promedio de todos los nmeros entregados. Recibe una cantidad variable de argumentos.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=MAX(nmero1;nmero2;...)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve el nmero mayor de todos los nmeros entregados. Recibe una cantidad variable de argumentos.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=MIN(nmero1;nmero2;...)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve el nmero menor de todos los nmeros entregados. Recibe una cantidad variable de argumentos.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=CONTAR(valor1;valor2;...)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve la cantidad de valores numricos entregados. Usualmente usado referenciando una rango de celdas para saber la cantidad de estas que tienen valores numricos.'}
                                    </TableCell>
                                </TableRow> 
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=CONTAR.SI(rango;criterio)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'A partir del rango entregado, cuenta la cantidad de celdas que cumplen con el criterio lgico entregado. Para una explicacin ms detallada revise los videos adjuntos.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=CONTARA(valor1;valor2;...)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve la cantidad de valores no vacios entregados. Usualmente usado referenciando una rango de celdas para saber la cantidad de estas que no estn vacias.'}
                                    </TableCell>
                                </TableRow>                                 
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
                <YoutubeLink embedId="CBPokSlpL-c"/>
                <p></p>
                <YoutubeLink embedId="8rCyl4q2K1g"/>
                <p><b>{Content["p7"]}</b></p>
                <p>{Content["p8"]}</p>
                <div className='unit-table'>
                    <TableContainer component={Paper} elevation={4}>
                        <Table size="small" aria-label="simple table">
                            <TableHead>
                            <TableRow>
                                <StyledTableCell>Funcin</StyledTableCell>
                                <StyledTableCell>Descripcin</StyledTableCell>
                            </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {"=CONCAT(texto1;texto2;...)"}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Concatena (une) los textos entregados, formando un solo texto. Recibe una cantidad variable de argumentos.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=IGUAL(texto1;texto2)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Compara el texto1 y texto2, entregando VERDADERO si son iguales o FALSO si son distintos.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=LARGO(texto)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve el largo del texto entregado, la cantidad de carcteres.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=MAYUSC(texto)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Transforma el texto entregado, escribiendolo solo con maysculas.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=MINUSC(texto)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Transforma el texto entregado, escribiendolo solo con minsculas.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=NOMPROPIO(texto)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Transforma el texto entregado, escribiendo con mayscula la primera letra de cada palabra y el resto en minsculas.'}
                                    </TableCell>
                                </TableRow>   
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=ENCONTRAR(texto1;texto2)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Busca el texto1 en el texto2 y entrega la posicin en que lo encuentra.'}
                                    </TableCell>
                                </TableRow>  
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=IZQUIERDA(texto;nmero)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Extrae la cantidad de carcteres especificada por nmero desde texto, iniciando de la izquierda.'}
                                    </TableCell>
                                </TableRow>   
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=DERECHA(texto;nmero)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Extrae la cantidad de carcteres especificada por nmero desde texto, iniciando de la derecha.'}
                                    </TableCell>
                                </TableRow>           
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
                <YoutubeLink embedId="0_JCfpQ07v0"/>
                <p><b>{Content["p9"]}</b></p>
                <p>{Content["p10"]}</p>
                <div className='unit-table'>
                    <TableContainer component={Paper} elevation={4}>
                        <Table size="small" aria-label="simple table">
                            <TableHead>
                            <TableRow>
                                <StyledTableCell>Funcin</StyledTableCell>
                                <StyledTableCell>Descripcin</StyledTableCell>
                            </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {"=AHORA()"}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Entrega la fecha actual junto con la hora.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=DIAS(fecha1;fecha2)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'A partir de la fecha1 y fecha2 entregadas, devuelve la cantidad de das que hay entre ambas.'}
                                    </TableCell>
                                </TableRow> 
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=FECHA(ao;mes;da)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve el formato fecha del ao, mes y da ingresados, los 3 de forma numrica.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=HOY()'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Entrega la fecha actual'}
                                    </TableCell>
                                </TableRow>        
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
                <YoutubeLink embedId="LcFGfkfp2lA"/>
                <p><b>{Content["p11"]}</b></p>
                <p>{Content["p12"]}</p>
                <div className='unit-table'>
                    <TableContainer component={Paper} elevation={4}>
                        <Table size="small" aria-label="simple table">
                            <TableHead>
                            <TableRow>
                                <StyledTableCell>Funcin</StyledTableCell>
                                <StyledTableCell>Descripcin</StyledTableCell>
                            </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {"=Y(comparacin1;[comparacin2];...)"}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Verifica todas las comparaciones entregadas y devuelve VERDADERO en caso que todas sean verdaderas, en cambio, devuelve FALSO.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=O(comparacin1;[comparacin2];...)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Verifica todas las comparaciones entregadas y devuelve VERDADERO en caso que alguna sea verdadera, en cambio, devuelve FALSO.'}
                                    </TableCell>
                                </TableRow> 
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=SI(comparacin;siVerdadero;siFalso)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Verifica la comparacin entregada, si sta es verdadera devuelve lo especificado en siVerdadero, en cambio, devuelve siFalso.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=SI.CONJUNTO(comparacin1;siVerdadero1;[comparacin2];[siVerdadero2];...)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Verifica por orden cada comparacin entregada, a la primera que encuentre verdadera, devuelve el siVerdadero correspondiente.'}
                                    </TableCell>
                                </TableRow>    
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
                <YoutubeLink embedId="vr4wiiqLHoA"/>
                <p></p>
                <YoutubeLink embedId="aA57N9kAeHM"/>
                <p><b>{Content["p13"]}</b></p>
                <p>{Content["p14"]}</p>
                <div className='unit-table'>
                    <TableContainer component={Paper} elevation={4}>
                        <Table size="small" aria-label="simple table">
                            <TableHead>
                            <TableRow>
                                <StyledTableCell>Funcin</StyledTableCell>
                                <StyledTableCell>Descripcin</StyledTableCell>
                            </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {"=BUSCARV(valorBuscado;matriz;numColumna)"}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'A partir de la matriz entregada, busca la fila en que se encuentra valorBuscado en la primera columna de la matriz, luego devuelve el valor de la misma fila pero de la columa numColumna.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=BUSCARH(valorBuscado;matriz;numFila)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'A partir de la matriz entregada, busca la columna en que se encuentra valorBuscado en la primera fila de la matriz, luego devuelve el valor de la misma columna pero de la fila numFila.'}
                                    </TableCell>
                                </TableRow> 
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=COINCIDIR(valor;rango;[tipoDeCoincidencia])'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Busca el valor en el rango entregado y devuelve el nmero de fila o columna en la que se encuentra. El tipo de coincidencia es para realizar una busqueda exacta o de otro tipo.'}
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=INDICE(rango;numFila;[numColumna])'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Entrega el valor de la celda que se encuentra en el nmero de fila y columna entregados, considerando el rango o matriz ingresado.'}
                                    </TableCell>
                                </TableRow>  
                                <TableRow>
                                    <TableCell component="th" scope="row">
                                        {'=UNICOS(rango)'}
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {'Devuelve los valores nicos de un rango dado.'}
                                    </TableCell>
                                </TableRow>   
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
                <YoutubeLink embedId="fb1WQsDvjkA"/>
                <p></p>
                <YoutubeLink embedId="N_k0JyQTCwo"/>
                <p></p>
                <YoutubeLink embedId="nNvTh77-3vo"/>
                <p></p>
                <YoutubeLink embedId="-7N2wPIEtxc"/>
            </div>
        </div>
    );
}

export default Funciones;