import React from 'react';
import YoutubeLink from "../../../YouTubeLink";
import '../../units.css';

const Teoria = () => {
    return (
        <div id='u4-teoria'>
            <div className='unit-subtitle'>
                <label>
                    Teora
                </label>
            </div>
            <div className='unit-content'>
                <p>
                Como se estudi en unidades anteriores, en Excel existe una variedad de funciones disponibles para el
                trabajo del usuario, as como <i>=promedio()</i>, <i>=buscarv()</i>, <i>=redondear()</i>, entre otras.
                </p> 
                <p>
                Estas funciones estn construidas con VBA, por lo que manejando este lenguaje es posible crear funciones propias desde cero,
                con un nombre personalizado y parmetros deseados. Para crearlas se debe trabajar en un mdulo (al igual que con las subrutinas)
                y se utilizar el procedimiento Function, el cual tiene una sintaxis similar a Sub.
                </p>
                <div className="unit-image">
                    <img src={"/images/U4T1F1.png"} className='unit-image' alt="Sintxis del procedimiento Function"/>
                </div>
                <p>
                Se inicia con la palabra reservada <b style={{color:'red'}}>Function</b>, continuando se ingresa el <b style={{color:'blue'}}>nombre</b> que se le dar a la funcin.
                Este nombre sigue las mismas reglas que los nombres de las variables y subrutinas. Dentro de los parntesis se ingresan los parmetros de la funcin, que pueden ser 1, 2 o ms. Es importante
                especificar que tipo de dato es cada parametro, es por esto que junto a cada uno ingresado se escribe la palabra
                reservada <b style={{color:'red'}}>as</b> junto con el tipo de dato que le corresponde al parmetro (Integer, String, etc.). Posterior a los
                parentesis se debe especificar el <b style={{color:'green'}}>tipo de dato del retorno</b> de la funcin, el cual corresponde al valor que entregar
                como resultado la funcin. Al igual que las subrutinas, dentro de Function se desarrollaran todas las instrucciones y
                se debe finalizar declarando una importante variable, cuyo nombre ser el <b style={{color:'blue'}}>nombre</b> de la funcin y su valor ser el <b style={{color:'green'}}>valor
                que queremos retornar</b> (el resultado de la funcin). Cabe destacar que en el procedimiento Function no es posible referenciar
                celdas del libro, por lo que cualquier dato requerido debe ser entregado como parmetro. Para cerrar la funcin se utilizan
                las palabras reservadas <b style={{color:'red'}}>End Function</b>.
                </p>
                <YoutubeLink embedId="2YXBPMnvAgg"/>
                <p>
                A continuacin se puede observar la creacin de una funcin llamada <i>sumaEnteros</i>, que recibe dos parmetros de tipo Integer (num1, num2) y
                retorna un valor de tipo Long.
                </p>
                <div className="unit-image2">
                    <img src={"/images/U4T1F2.png"} className='unit-image' alt="Ejemplo de una Function"/>
                </div>
                <p>
                Dentro de la funcin se crea la variable <i>resultadoSuma</i>, donde se almacena el resultado del procedimiento, por lo que
                su tipo coincide con el especificado en el encabezado de la funcin. Posteriormente se realiza el calculo y el resultado
                es almacenado en una variable de mismo nombre que la funcin. Esta ltima variable no es necesario crearla por medio de <i>Dim</i>.
                </p>
                <YoutubeLink embedId="CdCWa3a2_Ss"/>
                <p>
                Finalizada ya la funcin, es posible usarla de la misma forma que las funciones predefinidas de Excel
                </p>
                <div className="unit-image3">
                    <img src={"/images/U4T1F3.png"} className='unit-image' alt="Modo de uso de Functions"/>
                </div>
                <p>
                Las referencias estudiadas en la Unidad 1 funcionan perfectamente con las funciones creadas y, as como en la hoja de clculo, tambin se pueden utilizar la funciones creadas dentro de una subrutina. La lgica
                es la misma, se le entregan datos como parmetro y la funcin entrega un resultado, pero la sintxis cambia
                al utilizar la funcin en cdigo.
                </p>
                <div className="unit-image2">
                    <img src={"/images/U4T1F4.png"} className='unit-image' alt="Modo de uso de Functions en subrutinas"/>
                </div>
                <p>
                Como se puede observar, en la subrutina <i>sumar</i> se est haciendo uso de la funcin <i>sumaEnteros</i>, para
                esto se crean dos variables cuyo tipo de dato coincida con los parmetros, as como una variable cuyo tipo sea
                el del retorno. Se les asigna valores a las varibles <i>num1</i> y <i>num2</i>, que pueden ser explicitos o referenciados de alguna celda.
                La sintaxis del llamado de la funcin es igual que en la hoja de calculo, exceptuando que se deben reemplazar los punto y coma (;) por coma (,). Tambin,
                es necesario almacenar en alguna variable el retorno de la funcin, para poder trabajar luego con l, esto se realiza dandole como valor a la variable
                la funcin. En la subrutina <i>sumar</i>, se le asigna el retorno de la funcin a la variable <i>resultado</i>.
                </p>
                <YoutubeLink embedId="Rw68DE49FRg"/>
            </div>
        </div>
    );
}

export default Teoria;